
#reg{
    min-width: 100%;
    width:100%;
    margin: 0 auto;
    min-height: 100%;
    /*height: auto;*/
    /*height: 100%;*/
    /*position: relative;*/
}
.main{
    /*padding-bottom: 3.75rem;*/
}
.icon>img{
    margin-top: -0.25rem;
    width:3.75rem;
    height: 3.625rem;
}
.input-group{
    width:80%;
    margin: 0 auto;
    border-bottom:1px solid #F1F1F1;
}
.form-control{
    border-radius: 0px;
    border-style: none;
    /*border-bottom-style: solid;*/
    /*border-bottom-width:thin;*/
}
.send-button{
    font-size:.75rem;
    color:#1A73C4;
    /*height: 34px;*/
    border-style: none;
    /*border-bottom-style:solid;*/
    border-left-style:solid;
    /*border-bottom-width:thin;*/
}
.send-button>p{
    line-height: 1.5rem;
    color:#1A73C4;
}
.sended{
    color:#BDD6ED;
    border-radius: 0px;
}
.head-title{
    margin-top:60px;
    font-size: 1.875rem;
    color:#1A73C4;
    font-weight: bold;
}
.btn.disabled, .btn[disabled], fieldset[disabled] .btn{
    opacity:.45;
}
.head-title{
    margin-bottom: 3.125rem;
}
.head-title span{
    font-weight: blod;
}
.input-group-addon{
    background-color: white;
    border-radius: 0px;
    border-style: none;
    border-bottom-style:solid;
    border-bottom-width:thin;
}
.btn-validate{
    width:80%;
    color:white;
    margin-top: 2rem;
    border-radius: 0px;
    border-style: none;
    background-color:#1A73C4;
}
.copyright{
    font-size: .625rem;
    width:100%;
    margin: 0 auto;
    color:#828282;
    position:absolute;
    bottom:0;
    height: 3.75rem;
}



